/**
 * AI
 */

import { create } from 'zustand';
import { AIConfig, AIServiceStatus } from '../types/AITypes';
import { setDefaultAIService, getDefaultAIService } from '../utils/ai/aiService';
import { CardExpansionService } from '../utils/ai/cardExpansionService';
import { CardOrganizationService } from '../utils/ai/cardOrganizationService';
import { ICard, IConnection } from '../types/CoreTypes';
import { getCardsInViewport, ViewportInfo } from '../utils/ai/viewportUtils';

interface AIState {
  // AI
  config: AIConfig | null;
  isConfigured: boolean;
  
  // 
  status: AIServiceStatus;
  
  // 
  showConfigModal: boolean;
  configModalDefaultTab?: 'connection' | 'expansion' | 'organization' | 'draft';
  showDraftModal: boolean;
  
  // 
  setConfig: (config: AIConfig) => void;
  updateConfig: (updates: Partial<AIConfig>) => void;
  clearConfig: () => void;
  setShowConfigModal: (show: boolean, defaultTab?: 'connection' | 'expansion' | 'organization' | 'draft') => void;
  setShowDraftModal: (show: boolean) => void;
  
  // AI
  expandCards: (cards: ICard[], viewportInfo: ViewportInfo, customDescription?: string, temperature?: number) => Promise<ICard[]>;
  organizeCards: (cards: ICard[], viewportInfo: ViewportInfo, customDescription?: string, temperature?: number) => Promise<{ newCards: ICard[]; cardsToDelete: string[] }>;
  logicOrganizeCards: (cards: ICard[], connections: IConnection[], viewportInfo: ViewportInfo, customDescription?: string, temperature?: number) => Promise<{ mermaidCode: string }>;
  generateLogicDraft: (cards: ICard[], connections: IConnection[], viewportInfo: ViewportInfo, customDescription?: string, temperature?: number) => Promise<{ draftContent: string }>;
  
  // 
  setStatus: (status: Partial<AIServiceStatus>) => void;
  resetStatus: () => void;
}

// 
const loadConfigFromStorage = (): AIConfig | null => {
  try {
    const stored = localStorage.getItem('ai-config');
    if (stored) {
      const config = JSON.parse(stored);
      // 
      if (config.provider && config.apiKey) {
        return config;
      }
    }
  } catch (error) {
    console.warn('AI:', error);
  }
  return null;
};

// 
const saveConfigToStorage = (config: AIConfig | null): void => {
  try {
    if (config) {
      localStorage.setItem('ai-config', JSON.stringify(config));
    } else {
      localStorage.removeItem('ai-config');
    }
  } catch (error) {
    console.warn('AI:', error);
  }
};

export const useAIStore = create<AIState>((set, get) => {
  // 
  const initialConfig = loadConfigFromStorage();
  
  // AI
  if (initialConfig) {
    setDefaultAIService(initialConfig);
  }

  return {
    // 
    config: initialConfig,
    isConfigured: !!initialConfig,
    status: {
      isLoading: false,
      currentOperation: undefined,
      progress: undefined,
      error: undefined
    },
    showConfigModal: false,
    configModalDefaultTab: undefined,
    showDraftModal: false,

    // 
    setConfig: (config: AIConfig) => {
      // 
      saveConfigToStorage(config);
      
      // AI
      setDefaultAIService(config);
      
      set({
        config,
        isConfigured: true,
        showConfigModal: false
      });
    },

    // 
    updateConfig: (updates: Partial<AIConfig>) => {
      const { config } = get();
      if (!config) return;

      const newConfig = { ...config, ...updates };
      
      // 
      saveConfigToStorage(newConfig);
      
      // AI
      const aiService = getDefaultAIService();
      if (aiService) {
        aiService.updateConfig(updates);
      }
      
      set({ config: newConfig });
    },

    // 
    clearConfig: () => {
      saveConfigToStorage(null);
      set({
        config: null,
        isConfigured: false,
        showConfigModal: false
      });
    },

    // 
    setShowConfigModal: (show: boolean, defaultTab?: 'connection' | 'expansion' | 'organization' | 'draft') => {
      set({
        showConfigModal: show,
        configModalDefaultTab: show ? defaultTab : undefined
      });
    },

    // 
    setShowDraftModal: (show: boolean) => {
      set({
        showDraftModal: show
      });
    },

    // 
    expandCards: async (cards: ICard[], viewportInfo: ViewportInfo, customDescription?: string, temperature?: number): Promise<ICard[]> => {
      const { config } = get();
      
      if (!config) {
        set({
          status: {
            isLoading: false,
            error: 'AI'
          }
        });
        throw new Error('AI');
      }

      // 
      set({
        status: {
          isLoading: true,
          currentOperation: 'expand',
          progress: 0,
          error: undefined
        }
      });

      try {
        const aiService = getDefaultAIService();
        if (!aiService) {
          throw new Error('AI');
        }

        const expansionService = new CardExpansionService(aiService);
        
        // 
        set(state => ({
          status: { ...state.status, progress: 30 }
        }));

        const result = await expansionService.expandCardsInViewport(cards, viewportInfo, customDescription, temperature);
        
        if (!result.success) {
          throw new Error(result.error || '');
        }

        // 
        set(state => ({
          status: { ...state.status, progress: 70 }
        }));

        // 
        const newCards = expansionService.generateNewCards(
          result.data?.expandedCards || [],
          viewportInfo
        );

        // 
        set({
          status: {
            isLoading: false,
            currentOperation: undefined,
            progress: 100,
            error: undefined
          }
        });

        return newCards;

      } catch (error) {
        const errorMessage = error instanceof Error ? error.message : '';
        set({
          status: {
            isLoading: false,
            currentOperation: undefined,
            progress: undefined,
            error: errorMessage
          }
        });
        throw error;
      }
    },

    // 
    organizeCards: async (
      cards: ICard[],
      viewportInfo: ViewportInfo,
      customDescription?: string,
      temperature?: number
    ): Promise<{ newCards: ICard[]; cardsToDelete: string[] }> => {
      const { config } = get();
      
      if (!config) {
        set({
          status: {
            isLoading: false,
            error: 'AI'
          }
        });
        throw new Error('AI');
      }

      // 
      set({
        status: {
          isLoading: true,
          currentOperation: 'organize',
          progress: 0,
          error: undefined
        }
      });

      try {
        const aiService = getDefaultAIService();
        if (!aiService) {
          throw new Error('AI');
        }

        const organizationService = new CardOrganizationService(aiService);
        
        // 
        const cardsInViewport = getCardsInViewport(cards, viewportInfo);
        const cardsToDelete = cardsInViewport.map(card => card.id);
        
        // 
        set(state => ({
          status: { ...state.status, progress: 30 }
        }));

        const result = await organizationService.organizeCardsInViewport(cards, viewportInfo, customDescription, temperature);
        
        if (!result.success) {
          throw new Error(result.error || '');
        }

        // 
        set(state => ({
          status: { ...state.status, progress: 70 }
        }));

        // 
        const newCards = organizationService.generateOrganizedCards(
          result.data?.organizedCards || [],
          viewportInfo
        );

        // 
        set({
          status: {
            isLoading: false,
            currentOperation: undefined,
            progress: 100,
            error: undefined
          }
        });

        return { newCards, cardsToDelete };

      } catch (error) {
        const errorMessage = error instanceof Error ? error.message : '';
        set({
          status: {
            isLoading: false,
            currentOperation: undefined,
            progress: undefined,
            error: errorMessage
          }
        });
        throw error;
      }
    },

    // 
    logicOrganizeCards: async (cards: ICard[], connections: IConnection[], viewportInfo: ViewportInfo, customDescription?: string, temperature?: number): Promise<{ mermaidCode: string }> => {
      const { config } = get();

      if (!config) {
        set({
          status: {
            isLoading: false,
            error: 'AI'
          }
        });
        throw new Error('AI');
      }

      try {
        set({
          status: {
            isLoading: true,
            currentOperation: 'logicOrganize',
            progress: 10,
            error: undefined
          }
        });

        const aiService = getDefaultAIService();
        if (!aiService) {
          throw new Error('AI');
        }

        const { LogicOrganizationService } = await import('../utils/ai/logicOrganizationService');
        const logicService = new LogicOrganizationService(aiService);

        const result = await logicService.organizeLogicInViewport(
          cards,
          connections,
          viewportInfo,
          customDescription,
          temperature
        );

        if (!result.success) {
          throw new Error(result.error || '');
        }

        set({
          status: {
            isLoading: false,
            currentOperation: undefined,
            progress: 100,
            error: undefined
          }
        });

        return { mermaidCode: result.data?.mermaidCode || '' };

      } catch (error) {
        set({
          status: {
            isLoading: false,
            error: error instanceof Error ? error.message : ''
          }
        });
        throw error;
      }
    },

    // 
    generateLogicDraft: async (cards: ICard[], connections: IConnection[], viewportInfo: ViewportInfo, customDescription?: string, temperature?: number): Promise<{ draftContent: string }> => {
      const { config } = get();

      if (!config) {
        set({
          status: {
            isLoading: false,
            error: 'AI'
          }
        });
        throw new Error('AI');
      }

      try {
        set({
          status: {
            isLoading: true,
            currentOperation: 'logicDraft',
            progress: 10,
            error: undefined
          }
        });

        const aiService = getDefaultAIService();
        if (!aiService) {
          throw new Error('AI');
        }

        const { LogicDraftService } = await import('../utils/ai/logicDraftService');
        const logicDraftService = new LogicDraftService(aiService);

        const result = await logicDraftService.generateLogicDraftFromViewport(
          cards,
          connections,
          viewportInfo,
          customDescription,
          temperature
        );

        if (!result.success) {
          throw new Error(result.error || '');
        }

        set({
          status: {
            isLoading: false,
            currentOperation: undefined,
            progress: 100,
            error: undefined
          }
        });

        return { draftContent: result.data?.draftContent || '' };

      } catch (error) {
        set({
          status: {
            isLoading: false,
            error: error instanceof Error ? error.message : ''
          }
        });
        throw error;
      }
    },

    // 
    setStatus: (statusUpdate: Partial<AIServiceStatus>) => {
      set(state => ({
        status: { ...state.status, ...statusUpdate }
      }));
    },

    // 
    resetStatus: () => {
      set({
        status: {
          isLoading: false,
          currentOperation: undefined,
          progress: undefined,
          error: undefined
        }
      });
    }
  };
});
